import React, {FC, PropsWithChildren, useState} from 'react'
import { useCallback } from 'react';
import { useEffect } from 'react';
import styled from 'styled-components';
import {ContentContainer} from '../Container/Container'
import Footer from '../Footer/Footer'
import Header from '../Header/Header'
import { PromoBanner } from '../PromoBanner';

interface Props {
  disabledContainer?: boolean; 
}

const StyledLayout = styled.div`
height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
`

const Main = styled.main`
  height: 100%;
  flex: 1 0 auto;
`

const Layout:FC<PropsWithChildren<Props>> = ({children, disabledContainer}) => {
  const [hideBanner, setHideBanner] = useState(true)

  const handleBannerHide = useCallback(() => {
    sessionStorage.setItem("hideBanner", "true");
    setHideBanner(true);
  },[])

  useEffect(() => {
    const cachedHidedBanner = sessionStorage.getItem("hideBanner");
    if (!cachedHidedBanner) {
      setHideBanner(false)
    }
  }, [])

  return (
    <StyledLayout>
            {!hideBanner && <PromoBanner handleBannerHide={handleBannerHide}></PromoBanner>}
            <Header/>
            <Main>
            {disabledContainer ? children : <ContentContainer>
                {children}
            </ContentContainer>}
            </Main>
            <Footer/>
    </StyledLayout>

  )
}

export default Layout
