import { link } from "fs";
import { Link } from "gatsby";
import React, { FC } from "react";
import styled from "styled-components";
import { colors } from "../../../../../utils/colors";
import { device } from "../../../../../utils/layoutBreakpoints";
import { InternalLinks } from "../../../../../utils/links";
import { PrimaryButton, SecondaryButton } from "../../../Buttons/Buttons";
import CustomLink from "../../../Link";
import {
  headerNavigationItems,
  HeaderNavigationSections,
} from "../../HeaderNavigation/HeaderNavigationItem/headerNavigationItems";

const StyledServiceNavigation = styled.div`
  display: flex;
  gap: 25px;
  font-family: "Rubik";
  font-weight: 400;
  font-size: 0.9rem;
  align-items: center;
  @media ${device.desktop} {
    gap: 35px;
  }
`;

const ServiceNavigation: FC = () => {
  return (
    <StyledServiceNavigation>
      {headerNavigationItems
        .find(
          (section) =>
            section.sectionTitle === HeaderNavigationSections.ServiceNavigation
        )
        ?.links.filter(link => link.linkId !== "header_navigation_link_login").map((link) => {
          {
            return link.external ? (
              <a href={link.url} target="_blank" key={link.linkId}>
                {link.title}
              </a>
            ) : (
              <Link to={link.url} key={link.linkId}>
                {link.title}
              </Link>
            );
          }
        })}
      <Link to={InternalLinks.ContactUs}>
        <PrimaryButton height="auto" padding="10px 35px">
        Get a Demo
        </PrimaryButton>
      </Link>
    </StyledServiceNavigation>
  );
};

export default ServiceNavigation;
