import { Link } from "gatsby";
import React, { FC } from "react";
import styled, { css } from "styled-components";
import { NavigationItemLink } from "../../../../types/NavigationItem";
import { colors, opacityToHex } from "../../../../utils/colors";
import { device } from "../../../../utils/layoutBreakpoints";
import { Container } from "../../Container/Container";
import SeparatorLine from "../../Footer/SeparatorLine/SeparatorLine";
import {
  headerNavigationItems,
  HeaderNavigationSections,
} from "../HeaderNavigation/HeaderNavigationItem/headerNavigationItems";

const AnimationSelectorTemplate = (index: number, linksAmout?: number) => {
  return `
    p#header_navigation_link_${index} {
        animation-delay: ${
          linksAmout ? (linksAmout - index) * 0.025 : index * 0.1
        }s;
    };
    `;
};

const getFadeAnimation = (mode: "in" | "out") => {
  let styles = "";
  const linksAmount = headerNavigationItems.reduce(
    (acc, item) => acc + item.links.length,
    0
  );

  for (let i = 1; i <= linksAmount; i++) {
    styles += AnimationSelectorTemplate(i, mode === "in" ? 0 : linksAmount);
  }

  return styles;
};

const StyledMobileNavigation = styled.nav<Props>`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: ${colors.primary.white};
  position: fixed;
  z-index: 100;
  top: 60px;
  display: flex;
  visibility: ${(props) => (props.isOpen ? "visible" : "hidden")};
  font-family: "Rubik";
  transition: all 1.2s ease-in-out;
  max-height: ${(props) => (props.isOpen ? "100vh" : "0")};
  overscroll-behavior: contain;
  @media ${device.laptop} {
    display: none;
  }
`;

const MobileNavigationWrapper = styled.div<Props>`
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding-top: 40px;
  font-weight: 400;

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes fadeOut {
    from {
      opacity: 1;
    }
    to {
      opacity: 0;
    }
  }

  p {
    animation: ${(props) =>
      props.isOpen ? "fadeIn 1s ease-in" : "fadeOut 1s ease-in"};
  }

  ${(props) => {
    if (props.isOpen) return getFadeAnimation("in");
    return getFadeAnimation("out");
  }};

  & > div:first-child {
    font-size: 1.6rem;
    line-height: 3.2rem;
  }
  & > div:nth-child(2) {
    font-size: 1.2rem;
    line-height: 3.2rem;
    color: ${colors.primary.gray500};
  }
`;

interface Props {
  isOpen: boolean;
}

const NavigationLinks: FC<{ links: NavigationItemLink[] }> = ({ links }) => {
  return (
    <>
      {links.map((link, index, arr) => {
        return (
          <p key={link.linkId} id={link.linkId}>
            <div>
              {link.external ? (
                <a href={link.url} target="_blank" key={link.linkId}>
                  {link.title}
                </a>
              ) : (
                <Link to={link.url} key={link.linkId}>
                  {link.title}
                </Link>
              )}
            </div>
            {index !== arr.length - 1 && (
              <SeparatorLine
                margin="10px 0"
                backgroundColor={colors.secondary.gray300 + opacityToHex["0.3"]}
              />
            )}
          </p>
        );
      })}
    </>
  );
};

const MobileNavigation: FC<Props> = ({ isOpen }) => {
  return (
    <StyledMobileNavigation isOpen={isOpen}>
      <Container>
        <MobileNavigationWrapper isOpen={isOpen}>
          <div>
            <NavigationLinks
              links={
                headerNavigationItems.find(
                  (section) =>
                    section.sectionTitle ===
                    HeaderNavigationSections.MainNavigation
                )?.links as NavigationItemLink[]
              }
            />
          </div>
          <div>
            <NavigationLinks
              links={
                headerNavigationItems.find(
                  (section) =>
                    section.sectionTitle ===
                    HeaderNavigationSections.ServiceNavigation
                )?.links as NavigationItemLink[]
              }
            />
          </div>
        </MobileNavigationWrapper>
      </Container>
    </StyledMobileNavigation>
  );
};

export default MobileNavigation;
