import React from 'react';
import Facebook from "../Icons/Socials/Facebook";
import Instagram from "../Icons/Socials/Instagram";
import Twitter from "../Icons/Socials/Twitter";
import Linkedin from "../Icons/Socials/Linkedin";
import {SocialMediaButtonsWrapper} from "./styled";
import { ExternalLinks } from '../../../utils/links';

type SocialMediaButtonsProps = {
  color: string;
  opacity: number;
}

const SocialMediaButtons = ({ color, opacity } : SocialMediaButtonsProps) => {
  return (
    <SocialMediaButtonsWrapper>
      <a href={ExternalLinks.Facebook} target="_blank">
        <Facebook color={color} opacity={opacity} />
      </a>
      <a href={ExternalLinks.Instagram}  target="_blank">
        <Instagram color={color} opacity={opacity} />
      </a>
      <a href={ExternalLinks.Twitter} target="_blank">
        <Twitter color={color} opacity={opacity} />
      </a>
      <a href={ExternalLinks.Linkedin} target="_blank">
        <Linkedin color={color} opacity={opacity} />
      </a>
    </SocialMediaButtonsWrapper>
  );
};

export default SocialMediaButtons;
